/* GameMenu.css */

.game-menu {
  background-image: url("../../assets/images/imagen-fondo.png");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
 /*  background-color: #B8DBD9; */
  /* Color de fondo */
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  overflow: hidden;
}

.menu-content {
  width: 80%;
  max-width: 600px;
  /* background-color: #f5f5f5; */
  /* Color de fondo claro */
  border-radius: 20px;
  padding: 20px;
  /* box-shadow: 0 0 10px rgba(0, 0, 0, 0.5); */
  text-align: center;
}

.game-menu video {
  /* width: 100%; */
  height: 100vh;
  object-fit: cover;
}



/* Estilo para el título */
.titles-color {
  font-size: 48px;
  margin-bottom: 20px;
  display: inline-block;
  padding: 10px 20px;
  /* color: linear-gradient(to right, #ff6600, #ff9933); */
  /* Degradado de colores */
  color: snow;
  /* -webkit-text-fill-color: transparent; */
  /* Relleno de texto transparente */
  /* -webkit-text-stroke: 2px black; */
  /* Borde negro */
  border-radius: 5px;
}

/* Cambia el color de fondo del título al pasar el mouse */
.titles-color:hover {
  /* background: linear-gradient(to right, #ff9933, #ff6600); */
  /* Cambia el degradado de colores al pasar el mouse */
  color: #016fb9;
}

p {
  font-size: 18px;
  margin-bottom: 20px;
  /* color: #1a1a1a; */
  color: snow
}

.buttons-menu {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

.btn {
  background-color: #204473;
  /* Color del botón */
  color: #fff;
  border: none;
  border-radius: 5px;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  width: 300px;
}

.btn:hover {
  background-color: #33619e;
  /* Color al pasar el mouse */
}