.App {
  text-align: center;
}

.control-buttons {
  position: relative;
  z-index: 1000;
}

.container {
  margin: 20px auto;
  position: relative;
  height: 300px;
  width: 600px;
  overflow: hidden;
}

.puntaje-cont{
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  margin-right: 1rem;
}


.estaciones-cont{
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  margin-left: 1rem;
}

.marker-cont{
  position: absolute;
  bottom: 0;
  right: 0;
  display: flex;
  margin-right: 0.5rem;
}

.marker-img{
  width: 150px;
  height: auto;
}

.instructions-cont{
  position: fixed;
  bottom: 0;
  left: 50%; /* Centrar horizontalmente */
  transform: translateX(-50%); /* Ajustar para centrar horizontalmente */
  color: snow;
  text-align: center;
}

.o-hidden{
  display: none;
}

#contenedor-principal video{
  width: 100vw !important;
  height: 100vh !important;
  top: 0px !important;
  left: 0px !important;
  object-fit: cover;


}